.Header{
  text-align: center;
  margin-top: 50px;
  color:#efeded;
  font-size: 50px;
}

div .Box {
  font-size: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

  -ms-flex-pack: justify;

      justify-content: space-between;
  width: 550px;
  height: 500px;
  padding: 30px;
  margin: 20px auto;
  text-align: center;
  color:#efeded;
  border-bottom: solid #64E6BA 2px;
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  background: -webkit-gradient(linear,left top, left bottom,from(#212c33),color-stop(#40474c),to(#212c33));
  background: -webkit-linear-gradient(#212c33,#40474c,#212c33);
  background: -o-linear-gradient(#212c33,#40474c,#212c33);
  background: linear-gradient(#212c33,#40474c,#212c33);
  /* animation: fadein 2s; */
}

.Quote{
  margin-top: 50px;

  -webkit-transition-duration: 1s;

       -o-transition-duration: 1s;

          transition-duration: 1s;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;

}

.Author{
  font-size: 20px;
  font-style: italic;
  color:#d8d8d8;
}

.Divider{
  margin: 50px;
  height: 2px;
  background-color: #64E6BA;
}

.ButtonDiv {
  position: relative;
  display:-ms-flexbox;
  display:flex;

  -ms-flex-direction: row;

      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -webkit-transition: 3s;
  -o-transition: 3s;
  transition: 3s;
}

.ButtonStyle{
  border-radius: 50%;
  color: #64E6BA;
  background: transparent;
  border: 2px solid #64E6BA;
  cursor: pointer;
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  padding: 10px;

}
.ButtonStyle:hover{
  -webkit-transform:scale(.8);
      -ms-transform:scale(.8);
          transform:scale(.8);
}
.ButtonStyle:focus{
  outline: 0;
}
.Color {
  color: #64E6BA;
}
.quoteSize{
  font-size: 40px;
}



/* CSSTransitionGroup necessary classes*/

.fade-enter{
  opacity: 0;
}
.fade-enter-active{
  opacity: 1;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}
.fade-leave{
  opacity: 1;
}
.fade-leave-active{
  opacity: 0;
  -webkit-transition: opacity .2ms;
  -o-transition: opacity .2ms;
  transition: opacity .2ms;
}



/* Responsive breakpoints*/
@media only screen and (max-width: 630px){
  div.Box{
    width: 430px;
  }
}

@media only screen and (max-width: 490px) {
  .Header{
    font-size: 40px;
  }
  div.Box {
    width: 350px;
    padding: 15px;
    font-size: 22px;
    -ms-flex-line-pack: stretch;
        align-content: stretch;
    margin: 10px auto;

  }
  .quoteSize{
    font-size: 30px;
  }

  .ButtonDiv{

    -ms-flex-pack: distribute;

        justify-content: space-around;
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
  }

  .ButtonStyle{
    border-radius: 10px;
    margin: 10px 0;
  }
  .ButtonStyle:hover{
    -webkit-transform:scale(.93);
        -ms-transform:scale(.93);
            transform:scale(.93);
  }
}



